.box > input {
  width: 100%;
  padding: 10px 15px;
  border: 2px solid #333333;
  box-sizing: border-box;
  border-radius: 10px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0em;
  text-align: left;
  color: #ababab;
  outline-offset: 0px;
}
